<template>
  <div>
    <core-toolbarinstitution />
    <v-container
      fill-height
      fluid
      grid-list-xl
    >
      <v-layout
        justify-center
        wrap
      >
        <material-card
          color="green"
          title="Current Profile"
        >
          <v-form>
            <v-container py-0>
              <v-layout wrap>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="name"
                    type="string"
                    label="University Name"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="id"
                    label="University Id"
                    disabled
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="email"
                    label="Email"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="password"
                    label="Password"
                    :type="'password'"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md8
                >
                  <v-text-field
                    v-model="location.address"
                    label="Address"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="location.city"
                    label="City"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="location.country"
                    label="Country"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="phoneNumber"
                    label="Phone Number"
                  />
                </v-flex>
                <v-flex
                  xs12
                  text-xs-right
                >
                  <v-btn
                    class="mx-0 font-weight-light"
                    color="success"
                    href="/institutionloggedin/"
                    @click="updateInstitution"
                  >
                    Update Profile
                  </v-btn>
                </v-flex>

                <v-divider />

                <v-flex
                  xs12
                  text-xs-right
                >
                  <v-text-field
                    v-model="alumniName"
                    label="Alumni Name"
                  />
                  <v-btn
                    class="mx-0 font-weight-light d-inline"
                    color="blue"
                    @click="addAlumni"
                  >
                    Add Alumni
                  </v-btn>

                  <p 
                    v-if="isAlumniAddedShown"
                    class="d-inline"
                  >
                    Alumni Added
                  </p>
                </v-flex>
                <v-flex
                  xs12
                  text-xs-right
                >
                  <v-text-field
                    v-model="link"
                    label="Link url"
                  />
                  <v-btn
                    class="mx-0 font-weight-light d-inline"
                    color="blue"
                    @click="addLink"
                  >
                    Add Link
                  </v-btn>

                  <p
                    v-if="isUrlLinkAddedShown"
                    class="d-inline"
                  >
                    Link Added
                  </p>
                </v-flex>
                <v-flex>
                  <v-text-field
                    v-model="facebook"
                    label="Facebook url"
                  />
                  <v-btn
                    class="mx-0 font-weight-light d-inline"
                    color="blue"
                    @click="addFacebookLink"
                  >
                    Add Link
                  </v-btn>

                  <p
                    v-if="isFacebookLinkAddedShown"
                    class="d-inline"
                  >
                    Facebook Link Added
                  </p>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </material-card>
        <v-flex
          xs12
          md4
        />
      </v-layout>
    </v-container>
  </div>
</template>
<script>
   const axios = require('axios')

   export default {
       name: 'InstitutionLoggedIn',
       data () {
           return {
               id: '',
               name: '',
               phoneNumber: '1112222',
               email: '',
               password: '',
               location : {
                   city: '',
                   country: '',
                   address: ''
               },
               alumniName: '',
               link: '',
               facebook:'',
             isAlumniAddedShown: false,
             isUrlLinkAddedShown: false,
             isFacebookLinkAddedShown: false
           }
       },
       mounted () {
           const axiosConfig = {
               headers: {
                   'Content-Type': 'application/json',
                   'Authorization': 'Bearer ' + this.$store.state.token
               }
           };
           axios.get('http://localhost:8080/institutions', axiosConfig).then(resp => {
               console.log(resp);
               var institutionData = resp.data;
               this.name = institutionData.name;
               this.id = institutionData.id;
               this.email = institutionData.email;
               this.phoneNumber = institutionData.phoneNumber;
               this.password = institutionData.password;
               this.location.city = institutionData.location.city;
               this.location.country = institutionData.location.country;
               this.location.address = institutionData.location.address;
           }).catch(error => {
               console.log(error);
           })
       },
       methods: {
       addLink: async function(){
         let axiosConfig = {
           headers: {
               'Content-Type' : 'application/json',
               'Authorization' : 'Bearer ' + this.$store.state.token
           }
         };

         await axios.put('http://localhost:8080/institutions/addLink', this.link, axiosConfig)
                 .then((response) => {
                   console.log(response);
                   this.isUrlLinkAddedShown = true;
                   this.link = '';
                   setTimeout( () => {
                     this.isUrlLinkAddedShown = false
                   }, 2000)
                   }, (error) => {
                   console.log(error);
                 });
         },
         addFacebookLink: async function(){
           let axiosConfig = {
             headers: {
                 'Content-Type' : 'application/json',
                 'Authorization' : 'Bearer ' + this.$store.state.token
             }
           };

           await axios.put('http://localhost:8080/institutions/addFacebookLink', this.facebook, axiosConfig)
               .then((response) => {
                 console.log(response);
                 this.isFacebookLinkAddedShown = true;
                 this.facebook = '';
                 setTimeout( () => {
                   this.isFacebookLinkAddedShown = false
                 }, 2000)
                 }, (error) => {
                 console.log(error);
               });
           },
         addAlumni: async function(){
           let axiosConfig = {
             headers: {
                 'Content-Type' : 'application/json',
                 'Authorization' : 'Bearer ' + this.$store.state.token
             }
           };

           await axios.put('http://localhost:8080/institutions/addAlumni', this.alumniName, axiosConfig)
                 .then((response) => {
                   console.log(response);
                   this.isAlumniAddedShown = true;
                   this.alumniName = ''
                   setTimeout( () => {
                     this.isAlumniAddedShown = false
                   }, 2000)
                   }, (error) => {
                   console.log(error);
                 });
           }, updateInstitution() {
               const axiosConfig = {
                   headers: {
                       'Content-Type': 'application/json',
                       'Authorization': 'Bearer ' + this.$store.state.token
                   }
               };

               var request = {
                   name: this.name,
                   phoneNumber: this.phoneNumber,
                   email: this.email,
                   password: this.password,
                   location: {

                       city: this.location.city,
                       country: this.location.country,
                       address: this.location.address,
                       institutionId: '102'
                   }
               }

               axios.put('http://localhost:8080/institutions', request, axiosConfig).then(resp => {
                   console.log(resp);
               }).catch(error => {
                   console.log(error);
               });
           }
       }
   }
</script>
